<template>
  <div>
    <div class="container">
  <div class="container-head"> 
    <img src="../assets/metology-logo.png"/>
    <h1> Metology - HDataPred </h1>
  </div>

  <form action="post" id="metology-form">
    <div class="panel panel-primary">
      <div class="panel-heading">
        <h3 class="panel-title">基本資料</h3>
      </div>
      <div class="panel-body">
        <div id="input-group-a"></div>
      </div>
    </div>

    <div class="panel panel-success">
      <div class="panel-heading">
        <h3 class="panel-title">生活習慣</h3>
      </div>
      <div class="panel-body">
        <div id="input-group-other"></div>
      </div>
    </div>

    <div class="panel panel-info">
      <div class="panel-heading">
        <h3 class="panel-title">血液檢查</h3>
      </div>
      <div class="panel-body">
        <div id="input-group-b"></div>
      </div>
    </div>

    <div class="panel panel-warning">
      <div class="panel-heading">
        <h3 class="panel-title">肝功能</h3>
      </div>
      <div class="panel-body">
        <div id="input-group-d"></div>
      </div>
    </div>

    <div class="panel panel-danger">
      <div class="panel-heading">
        <h3 class="panel-title">尿液檢查</h3>
      </div>
      <div class="panel-body">
        <div id="input-group-f"></div>
      </div>
    </div>

    <div class="panel panel-default">
      <div class="panel-heading">
        <h3 class="panel-title">血脂肪</h3>
      </div>
      <div class="panel-body">
        <div id="input-group-g"></div>
      </div>
    </div>

    <button class="btn btn-default" type="button" data-toggle="modal" data-target=".bs-example-modal-lg">Predict</button>
  </form>


  </div>
</template>

<script type="text/javascript">
export default {
  name: 'HDataPred',
  mounted: function () {
    var aTypes = ['性別', '年齡', '身高', '體重', '收縮壓', '舒張壓', '體脂率', '腰圍']
    var aTypesEng = ['zgender', 'zage', 'aheight', 'aweight', 'astrsbp', 'astrdbp', 'abodyfat', 'awdth']
    var aTypesValue = [2, 67, 153, 46.2, 109, 52, 22.1, 68]

    var otherTypes = ['喝酒習慣', '工作強度', '運動與否']
    var otherTypesEng = ['b10dr', 'c16wk', 'c19sport']
    var otherTypesValue = [1, 2, 5]

    var bTypes = ['血色素', '血球容積', '白血球', '血小板', '空腹血糖']
    var bTypesEng = ['bhb', 'bhct', 'bwbc', 'bplt', 'cacsugar']
    var bTypesValue = [13.9, 40.6, 5.1, 265, 118]

    var dTypes = ['膽紅素總量', '直接膽紅素', '鹼性磷酸酶', 'GOT', 'GPT', 'r-胺基酸轉移酶']
    var dTypesEng = ['dtbil', 'ddbil', 'dalp', 'dsgot', 'dsgpt', 'drgt']
    var dTypesValue = [0.68, 0.14, 200, 17, 17, 36]

    var fTypes = ['尿素氮', '肌酸酐', '尿酸']
    var fTypesEng = ['fbun', 'fcre', 'fua']
    var fTypesValue = [15.1, 0.7, 5]

    var gTypes = ['三酸甘油脂', '總膽固醇', '甲型胎兒蛋白', 'C反應蛋白', 'HDL-C', 'LDL-C']
    var gTypesEng = ['gtg', 'gchol', 'jafp', 'lcrp', 'ghdlc', 'gldlc']
    var gTypesValue = [120, 261, 6, 0.2, 53, 184]

    var types = [aTypes, otherTypes, bTypes, dTypes, fTypes, gTypes]
    var typesEng = [aTypesEng, otherTypesEng, bTypesEng, dTypesEng, fTypesEng, gTypesEng]
    var typesValue = [aTypesValue, otherTypesValue, bTypesValue, dTypesValue, fTypesValue, gTypesValue]

    for (let j = 0; j < types.length; j++) {
      var type = types[j]
      var typeEng = typesEng[j]
      var typeValue = typesValue[j]

      if (j === 0) {
        var inputGroup = document.getElementById('input-group-a')
      } else if (j === 1) {
        inputGroup = document.getElementById('input-group-other')
      } else if (j === 2) {
        inputGroup = document.getElementById('input-group-b')
      } else if (j === 3) {
        inputGroup = document.getElementById('input-group-d')
      } else if (j === 4) {
        inputGroup = document.getElementById('input-group-f')
      } else {
        inputGroup = document.getElementById('input-group-g')
      }

      for (let i = 0; i < type.length; i++) {
        var newCol = document.createElement('div')
        newCol.className = 'col-md-3'

        var newinput = document.createElement('div')
        newinput.className = 'input-group'

        var newinputType = document.createElement('span')
        newinputType.className = 'input-group-addon'
        newinputType.innerHTML = type[i]

        var newinputInput = document.createElement('input')
        newinputInput.className = 'form-control'
        newinputInput.name = typeEng[i]
        newinputInput.value = typeValue[i]

        newinput.appendChild(newinputType)
        newinput.appendChild(newinputInput)
        newCol.appendChild(newinput)
        inputGroup.appendChild(newCol)
      }
    }
  }
}

</script>

<style type="text/css" scoped>
.input-group{
  padding: 10px 0px;
}

img{
  width: auto;
  height : auto;
  max-height: 30%;
  max-width: 30%;
}

.container-head {
  text-align: center;
  padding: 40px;
}

#metology-form {
  padding-bottom: 30px;

}
</style>




